.open-source-project-container {
    //background-color: rgb(59, 59, 59);
    //background-color: #c23231;
    background-color: white;
    min-height: calc(52vh);
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;

    a {
        color: inherit;
        text-decoration: none;
    }

    h1 {
        margin-top: 40px;
        color: white;
        height: 65px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: white;
        color: black;
        text-align: center;
        font-family: "Fredoka", sans-serif;
        font-weight: bold;
        letter-spacing: -1px;
        font-size: 32px;
        border-radius: 40px;
        box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
        -webkit-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);

        @media screen and(max-width:992px) {
            margin-top: 70px !important;
        }
    }

    .h2 {
        .icons {
            height: 60px !important;
            margin-top: 0px !important;
        }

        .icon-github {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: row;
        }

        .icon-npm {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: row;
        }
    }

    .open-source-project {
        .open-source-project-row {
            margin-top: 0px;
            margin-bottom: 0px;
            align-items: center;
            justify-content: center;
            display: flex;
            margin-top: 30px;
            margin-bottom: 60px;

            .open-source-project-item {
                height: 380px;
                margin-top: 40px;
                margin-bottom: 40px;
                padding: 0px 20px !important;


                .item-container {
                    background-color: white;
                    box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                    -webkit-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                    -moz-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                    border: 1px solid white !important;
                    background-color: white !important;
                    color: black !important;

                    .project-image-container {
                        width: 100%;
                        height: 180px;
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        img {
                            height: 70px;
                            width: auto;
                            object-fit: contain;
                        }
                    }

                    .project-key {
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        font-size: 1.5rem;
                        font-weight: bolder;
                        height: 50px;
                        margin-top: 10px;
                    }

                    .project-description {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        font-size: 1rem;
                        height: 75px;
                        padding: 0px 20px;
                        display: -webkit-box;
                        max-width: 100%;
                        margin: 0 auto;
                        -webkit-line-clamp: 3;
                        /* autoprefixer: off */
                        -webkit-box-orient: vertical;
                        /* autoprefixer: on */
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }

                    .project-link {
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        font-size: 1rem;
                        height: 80px;

                        a {
                            outline: none !important;
                            width: 84%;
                            font-weight: bold;
                            font-family: inherit;
                            border: 1px solid white;
                            height: 50px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            outline-color: none !important;
                            border-radius: 25px;

                            &:focus {
                                outline: none !important;
                                box-shadow: none !important;
                            }

                            &:hover {
                                background-color: white;
                                color: black;
                                border: 1px solid black;
                            }
                        }
                    }
                }
            }
        }
    }
}